    ResizeHelper(_width, _height) {
        return {
            WIDTH: _width,
            HEIGHT: _height,
            RATIO: _width / _height,
            widthProportion: function widthProportion(width) {
                return width / _this.WIDTH * 100;
            },
            heightProportion: function heightProportion(height) {
                return height / _this.HEIGHT * 100;
            },
            keyProperty: function keyProperty(aspect) {
                return _this.document.documentElement.clientWidth / _this.document.documentElement.clientHeight > _this.RATIO && aspect != MakeResizeAspect.COVER ? 'height' : 'width';
            }
        };
    }

