    calculateFont(resizeHelper, _aspect, fontSize, width, height) {
        var newFontSize = 0;
        if (resizeHelper.keyProperty(_aspect) == 'height') {
            newFontSize = height / resizeHelper.HEIGHT * fontSize;
        } else {
            newFontSize = width / resizeHelper.WIDTH * fontSize;
        }
        return newFontSize;
    }

    calculateFontCustom(fontSize, width, height) {
        var new_size = 0;

        if (height > width) {
            new_size = height / this.resizeHelper.HEIGHT * fontSize;
        }else{
            new_size = width / this.resizeHelper.WIDTH * fontSize;
        }
        
        return  new_size;
    }


    MakeResize(resizeHelper, config) {
        this.resizeHelper = resizeHelper;
        var _desiredRatio = resizeHelper.RATIO || 1000 / 600;
        _config = Object.assign({
            fontSize: 18,
            customClasses: {}
        }, config);

        _aspect = _config.aspect || MakeResizeAspect.CONTAIN;
        head = this.document.querySelector('head');
        style = this.document.createElement('style');
        head.appendChild(style);


        methods[MakeResizeAspect.CONTAIN] = function (actualWidth, actualHeight) {
            return actualWidth / actualHeight > _desiredRatio ? {
                width: actualHeight * _desiredRatio,
                height: actualHeight
            } : {
                width: actualWidth,
                height: actualWidth / _desiredRatio
            };
        };

        methods[MakeResizeAspect.COVER] = function (actualWidth, actualHeight) {
            return actualWidth / actualHeight > _desiredRatio ? {
                width: actualWidth,
                height: actualWidth / _desiredRatio
            } : {
                width: actualHeight * _desiredRatio,
                height: actualHeight
            };
        };

        this.window.addEventListener('resize', this.resize);
        setTimeout(function(){
            _this.resize();
        }, 100);

        
        
    }

    

