    changeFontSize(){
        $(this.document).find('p,h1,h2,h3,h4,h5,i,b,stronger').each(function(){
            var font_size_older; 
            var font_size_new;
            var font_size_px;

            if($(this).data('fontRef') != undefined || $(this).data('fontRef') != null){
                font_size_older = $(this).data('fontRef');
            }else{
                font_size_older = $(this).css('font-size');
            }

            
            $(this).data('fontRef', font_size_older);
            font_size_px = Math.round(font_size_older.split("px")[0]);
            font_size_new = _this.calculateFontCustom(font_size_px, _this.document.body.clientWidth, _this.document.body.clientHeight) * 0.063;
            $(this).css('font-size', font_size_new + "rem");
               
            
        });
    }

