    scaleTransformAll(div, config){
       
        var proportional = true; 
        // var currentWidth = div.outerWidth();
        // var currentHeight = div.outerHeight();

        var currentWidth = config.behaviors.width;
        var currentHeight = config.behaviors.height;
        
        var availableHeight = this.window.innerHeight;
        var availableWidth = this.window.innerWidth;

        var scaleX = availableWidth / currentWidth;
        var scaleY = availableHeight / currentHeight;
        
        if (proportional) {
            scaleX = Math.min(scaleX, scaleY);
            scaleY = scaleX;
        }
        
        // var translationX = Math.round((availableWidth/2) - ((currentWidth*scaleX)/2));
        // var translationY = Math.round((availableHeight/2) - ((currentHeight*scaleY)/2));

        div.css({
            "position": "fixed",
            "-webkit-box-shadow": "0px 0px 14px 6px rgba(0,0,0,0.44)", 
            "box-shadow": "0px 0px 14px 6px rgba(0,0,0,0.44)",
            "left": "0px",
            "top": "0px",
            "min-width": currentWidth + "px",
            "min-height": "auto",
            "-webkit-transform": "translate(" + 0 + "px, "
                                            + 0 + "px) scale3d("
                                            + scaleX + ", " + scaleY + ", 1)",
            "-webkit-transform-origin": "0 0"
        }); 

    
    }

