    removeRule() {
        if (typeof _this.window.CSSMediaRule !== 'function') {
            return false;
        }
        var styleSheets = _this.document.styleSheets;
        var number = 0;
    
        if (!styleSheets) {
            return false;
        }
    
        for (var i = 0; i < styleSheets.length; i++) {
            var styleSheet = styleSheets[i];
            var rules = styleSheet.cssRules;
            if (!rules) {
                continue;
            }
            for (var j = 0; j < rules.length; j++) {
                var cssText = rules[j].cssText;
                var ruleStyle = rules[j].style;

               
                // if(ruleStyle!=undefined){
                //     if(ruleStyle.height == "100vh"){
                //         ruleStyle.height = "100%";
                //     } 
                // }

                // if(ruleStyle!=undefined){
                //     if(ruleStyle.width == "100vw"){
                //         ruleStyle.width = "100%";
                //     } 
                // }
                
                if (cssText.indexOf('@media') === 0) {
                    number++;
                    styleSheet.deleteRule(j);
                }
            }
        }
        if (number) {
            return number;
        }
        return 0;
    }
    
    removeMediaQueries() {
        var num = _this.removeRule();
        var total = num;
        while (num) {
            num = _this.removeRule();
            total += num;
        }
        if (total) {
            // console.info(total + ' media quer' + (total == 1 ? 'y' : 'ies' + ' removed'));
        } else {
            // console.info('No media queries removed');
        }
    }

    

